import React from "react";
import Header from "../components/header";
import {Link} from "react-router-dom";
import '../stylesheets/chart.css';
import Footer from "../components/footer";
import List from "../components/chartList";
import Update from "../components/update";
import PlayURL from "../components/getPlaylist";


class ChartsHour extends React.Component {
    render() {
        return (
            <div className="container">
                <Header/>
                <hr className="chart-menu-separate"/>
                <div className="chart-menu">
                    <Link to="/charts" className="chart-menu-text">TOP100</Link>
                    <Link to="/charts/hourly" className="chart-menu-text"></Link>
                    <Link to="/charts/daily" className="chart-menu-text selected"></Link>
                    <Link to="/charts/weekly" className="chart-menu-text"></Link>
                    <Link to="/charts/monthly" className="chart-menu-text"></Link>
                </div>
                <hr className="chart-menu-separate"/>
                <div className="fade-in-page">
                    <div className="chart-type"></div>
                    <Update type="daily"/>
                    <PlayURL type="daily"/>
                    <hr className="section-separate"/>
                    <div className="chart-wrap">
                        <div className="chart-header">
                            <span id="header-pos"></span>
                            <span id="header-title"></span>
                            <span id="header-artist"></span>
                            <div className="header-right-align-chart">
                                <span id="header-peak"></span>
                                <span id="header-date"></span>
                                <span id="header-views"></span>
                            </div>
                        </div>
                        <hr className="chart-separate-line"/>
                        <List type="daily" limit={100}/>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}

export default ChartsHour;