import React from "react";
import Header from "../components/header";
import wakgood from "../images/wakgood.png";
import {Link} from "react-router-dom";
import '../stylesheets/chart.css';
import NewAlbum from "../components/new";
import Footer from "../components/footer";
import List from "../components/chartList";


class Main extends React.Component {
    render() {
        return (
            <div className="container">
                <Header/>
                <div className="fade-in-page">
                    <h1 className="title">  , </h1>
                    <div className="title-image-wrap">
                        <a href="https://cafe.naver.com/steamindiegame/5511558" target="_blank" rel="noreferrer">
                        <img src={wakgood} alt="wakgood" className="title-image fade-in-page"/></a>
                        <span className="title-image-desc">Art by </span>
                    </div>
                    <p className="title-desc">
                        
                        
                        
                        
                        
                        
                        
                        
                        
                        
                        
                    </p>
                    <h2 className="section-title"> </h2>
                    <Link to="/new" className="more">  <i className="fa-solid fa-angle-right"/></Link>
                    <hr className="section-separate"/>
                    <NewAlbum maximum={2}/>

                    <h2 className="section-title"> </h2>
                    <Link to="/charts" className="more">  <i className="fa-solid fa-angle-right"/></Link>
                    <hr className="section-separate"/>
                    <div className="chart-wrap">
                        <div className="chart-header">
                            <span id="header-pos"></span>
                            <span id="header-title"></span>
                            <span id="header-artist"></span>
                            <div className="header-right-align">
                                <span id="header-last"></span>
                                <span id="header-peak"></span>
                                <span id="header-date"></span>
                                <span id="header-views"></span>
                            </div>
                        </div>
                        <hr className="chart-separate-line"/>
                        <List type="total" limit={10}/>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}

export default Main;